<template>
  <div>
    <div class="container flex pb-3 mx-auto mt-10 align-middle border-b border-gray-600 rwd">
            <img :src="posterPath" class="w-64 h-auto" alt="">
    <div class="ml-24">
        <h1 class="text-4xl font-semibold">{{ this.movie.title }}</h1>
        <h1 v-if="movie.original_title != movie.title" class="text-2xl font-semibold">{{ this.movie.original_title }}</h1>
        <span class="flex mt-4 text-sm leading-9 text-gray-400">
            <svg class="w-6 mr-1 text-yellow-500 fill-current" fill="#ffffff" viewBox="0 0 1024 1024" xmlns="http://www.w3.org/2000/svg" ><g id="SVGRepo_bgCarrier" stroke-width="0"></g><g id="SVGRepo_tracerCarrier" stroke-linecap="round" stroke-linejoin="round"></g><g id="SVGRepo_iconCarrier"> <path d="M908.1 353.1l-253.9-36.9L540.7 86.1c-3.1-6.3-8.2-11.4-14.5-14.5-15.8-7.8-35-1.3-42.9 14.5L369.8 316.2l-253.9 36.9c-7 1-13.4 4.3-18.3 9.3a32.05 32.05 0 0 0 .6 45.3l183.7 179.1-43.4 252.9a31.95 31.95 0 0 0 46.4 33.7L512 754l227.1 119.4c6.2 3.3 13.4 4.4 20.3 3.2 17.4-3 29.1-19.5 26.1-36.9l-43.4-252.9 183.7-179.1c5-4.9 8.3-11.3 9.3-18.3 2.7-17.5-9.5-33.7-27-36.3z"></path> </g></svg>
            <span class="text-2xl text-yellow-500">
                {{  voteAverageConverter }}
            </span>
            <span  class="mr-1 text-yellow-500">
                /10 
            </span>
            ({{ this.movie.vote_count }}個評分) | 上映日期:{{ this.movie.release_date }} | 片長:{{ this.movie.runtime }} 分鐘

            <span :key="index" v-for="(item, index) in movie.genres" class="ml-1">
                {{ item.name }}
                <span v-if="movie.genres.length -1 != index">,</span>
            </span>
        </span>
        <p class="mt-5">
            {{ this.movie.overview }}
        </p>

        <div class="mt-5">
            <span class="mt-5 font-semibold">Featured Cast</span>

            <div class="flex">
                <div 
                    :key="index"
                    v-for="(crew, index) in movie.credits.crew"
                >
                    <div v-if="index < 2" class="flex flex-col mt-5 mr-5">
                        <span>{{ crew.name }}r</span>
                        <span class="text-gray-500">{{ crew.job }}</span>
                    </div> 
                </div>
            </div>
        </div>
        <div class="mt-5 ml-5">
            <a
            @click.prevent="openYouTubeModel"
            target="blank"
             class="inline-flex px-5 py-3 text-black bg-yellow-500 rounded cursor-pointer"
             >
             <svg class="w-6 fill-current" fill="#000000" viewBox="0 0 56 56" xmlns="http://www.w3.org/2000/svg"><g id="SVGRepo_bgCarrier" stroke-width="0"></g><g id="SVGRepo_tracerCarrier" stroke-linecap="round" stroke-linejoin="round"></g><g id="SVGRepo_iconCarrier"><path d="M 27.9999 51.9063 C 41.0546 51.9063 51.9063 41.0781 51.9063 28 C 51.9063 14.9453 41.0312 4.0937 27.9765 4.0937 C 14.8983 4.0937 4.0937 14.9453 4.0937 28 C 4.0937 41.0781 14.9218 51.9063 27.9999 51.9063 Z M 27.9999 47.9219 C 16.9374 47.9219 8.1014 39.0625 8.1014 28 C 8.1014 16.9609 16.9140 8.0781 27.9765 8.0781 C 39.0155 8.0781 47.8983 16.9609 47.9219 28 C 47.9454 39.0625 39.0390 47.9219 27.9999 47.9219 Z M 23.8749 36.6250 L 36.4140 29.2188 C 37.3280 28.6563 37.3046 27.3672 36.4140 26.8516 L 23.8749 19.3984 C 22.9140 18.8359 21.6483 19.2812 21.6483 20.3359 L 21.6483 35.6875 C 21.6483 36.7656 22.8436 37.2344 23.8749 36.6250 Z"></path></g></svg>
             <span class="ml-3">觀看預告</span> 
            </a>

            <a
             class="inline-flex py-3 ml-5 text-black bg-yellow-500 rounded px-7"
             >
             <!-- <img src="@/assets/images/heartwhite.png" alt="" /> -->
             <svg class="w-5 fill-current" fill="#000000" viewBox="0 0 1920 1920" xmlns="http://www.w3.org/2000/svg"><g id="SVGRepo_bgCarrier" stroke-width="0"></g><g id="SVGRepo_tracerCarrier" stroke-linecap="round" stroke-linejoin="round"></g><g id="SVGRepo_iconCarrier"> <path d="m1692.48 910.647-732.762 687.36-731.182-685.779c-154.616-156.875-154.616-412.122 0-568.997 74.542-75.558 173.704-117.233 279.304-117.233h.113c105.487 0 204.65 41.675 279.078 117.233l.113.113c74.767 75.783 116.103 176.865 116.103 284.385h112.941c0-107.52 41.224-208.602 116.104-284.498 74.428-75.558 173.59-117.233 279.19-117.233h.113c105.487 0 204.763 41.675 279.19 117.233 154.617 156.875 154.617 412.122 1.695 567.416m78.833-646.701c-95.887-97.355-223.737-150.89-359.718-150.89h-.113c-136.094 0-263.83 53.535-359.604 150.777-37.61 38.061-68.443 80.979-92.16 127.398-23.718-46.42-54.664-89.337-92.16-127.285-95.774-97.355-223.51-150.89-359.605-150.89h-.113c-135.981 0-263.83 53.535-359.83 150.89-197.648 200.696-197.648 526.983 1.694 729.035l810.014 759.868L1771.313 991.4c197.647-200.47 197.647-526.758 0-727.454" fill-rule="evenodd"></path> </g></svg>            
             <span class="ml-3">Favorite</span> 
            </a>
        </div>
        

    </div>
    </div>
   
        <CastMember :casts="movie.credits.cast"/> 
        <ImageData 
            :images="movie.images.backdrops" 
            v-on:on-image-click="showImageModel" 
        /> 
        <MediaModel 
            v-model="modelOpen" 
            :mediaURL="mediaURL" 
            :isVideo="this.isVideo"
        />
       
  </div>
</template>

<script>
import CastMember from './CastMember.vue';
import ImageData from './ImageData.vue';
import MediaModel from '../models/MediaModel.vue';
export default {
    components: {
        CastMember,
        ImageData,
        MediaModel,
    },
    data() {
        return {
            movie: {
                credits: {
                    crew: {},
                },
                images: {
                    backdrops: {},
                },
            },
            modelOpen: false,
            isVideo: false,
            mediaURL: "",
        };
    },

    // mounted(){
    //     // console.log(this.$route.params.id);
    //     this.fetchmovie(this.$route.params.id);
    // },

    watch: {
        "$route.params.id": {
            handler() {
                this.fetchMovie(this.$route.params.id);
            },
            immediate: true,
        },
    },

    methods: {
        async fetchMovie(movieId) {
            const response = await this.$http.get(
                "/movie/" + movieId + "?append_to_response=credits,videos,images&language=zh-TW&include_image_language=en,null&include_video_language=en,null"
            );

            // console.log(response.data);
            this.movie = response.data;
        },

        openYouTubeModel() {
            this.mediaURL = this.youtubeVideo();
            this.isVideo = true;
            this.modelOpen = true;
        },
        openImageModel() {
            this.isVideo = false;
            this.modelOpen = true;
        },
        youtubeVideo() {
            if (!this.movie.videos) return;
            return (
                "https://www.youtube.com/embed/" + this.movie.videos.results[0].key
            );
        },
        showImageModel(imageFullPath) {
            this.mediaURL = imageFullPath;
            this.isVideo = false;
            this.modelOpen = true;
        }
    },

    computed: {
    posterPath() {
      return "https://image.tmdb.org/t/p/w500" + this.movie.poster_path;
    },
    voteAverageConverter() {
        let average = (Math.round(this.movie.vote_average * 10) / 10).toString();
            if (average.indexOf(".") < 0) {
                average += ".0";
                return average;
            } else {
                return average;
            }
     },    
  },

};

   
</script>

